import React, { useState } from "react";
import {
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  Divider,
  Typography,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Badge,
} from "@mui/material";
import CancelIcon from '@mui/icons-material/Cancel';
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import MoreHorizIcon from "@mui/icons-material/MoreHoriz";

const ordersData = [
  {
    id: 1,
    customer: "Customer 1",
    total: 100,
    date: "2023-01-01",
    status: "pending",
  },
  {
    id: 2,
    customer: "Customer 2",
    total: 150,
    date: "2023-01-02",
    status: "confirmed",
  },
  {
    id: 3,
    customer: "Customer 3",
    total: 200,
    date: "2023-01-03",
    status: "canceled",
  },
  // Thm d liu n hng khc ti y...
];

const Order = () => {
  const [selectedOrder, setSelectedOrder] = useState(null);

  const handleViewDetails = (order) => {
    setSelectedOrder(order);
  };

  const handleCloseDetails = () => {
    setSelectedOrder(null);
  };

  const renderStatusBadge = (status) => {
    let color, icon;

    switch (status) {
      case "pending":
        color = "orange";
        icon = <MoreHorizIcon />;
        break;
      case "confirmed":
        color = "green";
        icon = <CheckCircleIcon />;
        break;
      case "canceled":
        color = "gray";
        icon = <CancelIcon />;
        break;
      default:
        color = "default";
        icon = null;
    }

    return (
      <Badge color={color} variant="dot">
        {icon}
      </Badge>
    );
  };

  return (
    <>
      <List>
        {ordersData.map((order) => (
          <React.Fragment key={order.id}>
            <ListItem button onClick={() => handleViewDetails(order)}>
              <ListItemAvatar>
                <Avatar>
                  {/* Icon hoc hnh nh i din cho n hng */}
                  <Typography>{order.id}</Typography>
                </Avatar>
              </ListItemAvatar>
              <ListItemText
                primary={`Order ${order.id}`}
                secondary={`Total: $${order.total} - Date: ${order.date}`}
              />
              <div>{renderStatusBadge(order.status)}</div>
            </ListItem>
            <Divider />
          </React.Fragment>
        ))}
      </List>

      <Dialog open={Boolean(selectedOrder)} onClose={handleCloseDetails}>
        <DialogTitle>Order Details</DialogTitle>
        <DialogContent>
          {selectedOrder && (
            <div>
              <DialogContentText>
                Customer: {selectedOrder.customer}
              </DialogContentText>
              <DialogContentText>
                Total: ${selectedOrder.total}
              </DialogContentText>
              <DialogContentText>Date: {selectedOrder.date}</DialogContentText>
              {/* Thm thng tin khc ca n hng ti y... */}
            </div>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDetails}>Close</Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default Order;
